@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Oswald", sans-serif;
}

.colorContainer {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.text {
  cursor: pointer;
}

.myColor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25vw;
  height: 50vh;
  transition: 0.5s ease-in-out;
  transform: scaleX(0);
}

.myColorActivated {
  transform: scaleX(1);
}

.buttonRefresh {
  position: absolute;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  top: calc(50% - 14px);
  left: calc(50% - 50px);
  background-color: white;
  z-index: 10;
  width: 100px;
  height: 28px;
  border-radius: 13px;
  border: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  transform-origin: center;
}

.buttonRefresh:focus {
  outline: none;
}

.buttonRefreshClicked {
  left: calc(50% - 14px);
  width: 28px;
  height: 28px;
  border-radius: 13px;
}

.spanButton {
  opacity: 1;
  transition: 0.3s 0.3s;
}

.spanButtonClicked {
  opacity: 0;
}

.background {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  z-index: 10;
}
.backgroundDesactivated {
  opacity: 0;
  transform: scaleY(0);
  transition: 0.3s ease-in-out;
}

.backgroundActivated {
  opacity: 1;
  transform: scaleY(1);
  transition: 0.3s ease-in-out;
}

.textCopied {
  font-size: 70px;
  color: white;
}

.appContainer {
  position: relative;
  height: 100vh;
}

.colorsContainer {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 25vw);
  grid-template-rows: repeat(2, 50vh);
}


