.background {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  z-index: 10;
}
.backgroundDesactivated {
  opacity: 0;
  transform: scaleY(0);
  transition: 0.3s ease-in-out;
}

.backgroundActivated {
  opacity: 1;
  transform: scaleY(1);
  transition: 0.3s ease-in-out;
}

.textCopied {
  font-size: 70px;
  color: white;
}
